.gamer {
  position: relative;

  overflow: hidden;
  padding: 2px;
  filter: hue-rotate(0deg);
  transition: all 0.3s linear;
}
/* .gamer:hover {
	box-shadow:0px 0px 5px 0px;
} */

.gamer:hover span {
  position: absolute;
  display: block;
}
.gamer:hover span:nth-child(1) {
  filter: hue-rotate(0deg);
  top: 0;
  left: 0;
  width: 100%;
  height: 10px;
  background: linear-gradient(90deg, cyan,magenta);
  animation: animate1 0.8s linear infinite;
}
@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}
.gamer:hover span:nth-child(2) {
  filter: hue-rotate(60deg);
  top: -100%;
  right: 0;
  width: 10px;
  height: 100%;
  background: linear-gradient(180deg, cyan, yellow);
  animation: animate2 0.8s linear infinite;
  animation-delay: 0.2s;
}
@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}
.gamer:hover span:nth-child(3) {
  /* filter: hue-rotate(120deg); */
  bottom: 0;
  right: -100%;
  height: 10px;
  width: 100%;
  background: linear-gradient(270deg, yellow,lime);
  animation: animate3 0.8s linear infinite;
  animation-delay: 0.4s;
}
@keyframes animate3 {
  0% {
    right: -100%;
    /* height: 10px; */
  }
  50%,
  100% {
    /* height: 10px; */
    right: 100%;
  }
}
.gamer:hover span:nth-child(4) {
  filter: hue-rotate(300deg);
  bottom: -100%;
  left: 0;
  width: 10px;
  height: 100%;
  background: linear-gradient(360deg,lime, cyan);
  animation: animate4 0.8s linear infinite;
  animation-delay: 0.6s;
}
@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
